/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.content {
  max-width: 85rem;
  margin: 2rem 1rem;
}
@media (min-width: 768px) {
  .content {
    width: 90%;
    margin: 2rem auto;
  }
}

@media (min-width: 768px) {
  .dos-tercios {
    width: 60%;
  }
}

@media (min-width: 768px) {
  .tercios {
    width: 30%;
  }
}

.text-center {
  text-align: center;
}

.flex {
  display: flex;
}

@media (min-width: 768px) {
  .medios {
    width: 40%;
  }
}

@media (min-width: 768px) {
  .flex-row {
    display: flex;
    justify-content: space-between;
  }
}

.header {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5fadd4+0,60a9d2+29,61afd6+66,7db9e8+98 */
  background: #5fadd4;
  /* Old browsers */
  background: -moz-linear-gradient(-45deg, #5fadd4 0%, #60a9d2 29%, #61afd6 66%, #7db9e8 98%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #5fadd4 0%, #60a9d2 29%, #61afd6 66%, #7db9e8 98%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, #5fadd4 0%, #60a9d2 29%, #61afd6 66%, #7db9e8 98%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#5fadd4", endColorstr="#7db9e8",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  height: 100vh;
}
.header .title {
  text-transform: uppercase;
}

.section .title-container {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5368ab+0,5fb1d7+98 */
  background: #5368ab;
  /* Old browsers */
  background: -moz-linear-gradient(top, #5368ab 0%, #5fb1d7 98%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #5368ab 0%, #5fb1d7 98%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #5368ab 0%, #5fb1d7 98%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#5368ab", endColorstr="#5fb1d7",GradientType=0 );
  /* IE6-9 */
  text-transform: uppercase;
  padding: 2rem;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid #1a001a;
}
.section .title-container h3 {
  margin: 2rem 0 0 0;
}

.row .title-card {
  text-align: center;
  font-weight: 700;
  font-size: 2rem;
}

.card-beneficios {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2rem;
  border-radius: 4rem;
  border: 2px solid #1d6fbb;
}
.card-beneficios .beneficios {
  align-items: center;
}
.card-beneficios .beneficios picture {
  margin: 1.5rem;
}
.card-beneficios .beneficios .slag {
  color: #1d6fbb;
}
.card-beneficios .beneficios .description {
  color: #1a1b15;
}

.card-caracteristicas {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2rem;
  border-radius: 4rem;
  border: 2px solid #1d6fbb;
}
.card-caracteristicas .caracteristicas {
  display: flex;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .card-caracteristicas .caracteristicas {
    width: 50%;
  }
}
.card-caracteristicas .caracteristicas picture {
  margin: 2rem;
  width: 7rem;
}

.qr {
  margin: 2rem auto;
  border: 2px solid #1a1b15;
  border-radius: 4rem;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 30rem;
  width: 30rem;
}
@media (min-width: 768px) {
  .qr {
    width: 100%;
    height: auto;
  }
}

.etiqueta {
  margin: 2rem auto;
  border: 2px solid #1d6fbb;
  border-radius: 4rem;
  text-align: center;
}
.etiqueta h3 {
  color: #1a1b15;
  margin: 2rem;
  text-transform: uppercase;
}

.mant-mensual {
  text-align: left;
  margin: 0;
  font-size: 2rem;
}