@use "base/variables" as v;
@use "base/mixins" as m;

.header {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5fadd4+0,60a9d2+29,61afd6+66,7db9e8+98 */
    background: #5fadd4; /* Old browsers */
    background: -moz-linear-gradient(-45deg,  #5fadd4 0%, #60a9d2 29%, #61afd6 66%, #7db9e8 98%); /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg,  #5fadd4 0%,#60a9d2 29%,#61afd6 66%,#7db9e8 98%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg,  #5fadd4 0%,#60a9d2 29%,#61afd6 66%,#7db9e8 98%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#5fadd4', endColorstr='#7db9e8',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    height: 100vh;

    .title{
        text-transform: uppercase;
    }

}
