@use 'variables' as v;
@use 'mixins' as m;

.seccion {
    margin-top: v.$separacion / 2;
    margin-bottom: v.$separacion / 2;
}
.contenido-centrado {
    max-width: 800px;
}
.content{
    max-width: 85rem;
    margin: 2rem 1rem;
    @include m.tablet{
        width: 90%;
        margin: 2rem auto;
    }
}
.dos-tercios{
    @include m.tablet{
        width: 60%;
    }
}

.tercios{
    @include m.tablet{
        width: 30%;
    }
}
.text-center{
    text-align: center;
}

.flex{
    display: flex;
}
.medios{
    @include m.tablet{
        width: 40%;
    }
}
.flex-row{
    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }
}