@use "base/variables" as v;
@use "base/mixins" as m;

.section{
    .title-container{
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5368ab+0,5fb1d7+98 */
        background: #5368ab; /* Old browsers */
        background: -moz-linear-gradient(top,  #5368ab 0%, #5fb1d7 98%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top,  #5368ab 0%,#5fb1d7 98%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom,  #5368ab 0%,#5fb1d7 98%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#5368ab', endColorstr='#5fb1d7',GradientType=0 ); /* IE6-9 */
        
        text-transform: uppercase;
        padding: 2rem;
        padding-bottom: .5rem;
        border-bottom: 3px solid v.$morado;
        
        h3{
            margin: 2rem 0 0 0;
        }
    }
}

.row{
    .title-card{
        text-align: center;
        font-weight: v.$bold;
        font-size: 2rem;
    }
}
.card-beneficios{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 2rem;
    border-radius: 4rem;
    border: 2px solid v.$temaCatalogo;
    .beneficios{
        align-items: center;
        picture{
            margin: 1.5rem;
        }
        .slag{
            color: v.$temaCatalogo;
        }
        .description{
            color: v.$negro;
        }
    }
}
.card-caracteristicas{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 2rem;
    border-radius: 4rem;
    border: 2px solid v.$temaCatalogo;
    .caracteristicas{
        display: flex;
        align-items: center;
        width: 100%;

        @include m.tablet{
            width: 50%;
        }

        picture{
            margin: 2rem;
            width: 7rem;
        }
    }
}

.qr{
    margin: 2rem auto;
    border: 2px solid v.$negro;
    border-radius: 4rem;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 30rem;
    width: 30rem;
    @include m.tablet{
        width: 100%;
        height: auto;
    }
}
.etiqueta{
    margin: 2rem auto;
    border: 2px solid v.$temaCatalogo;
    border-radius: 4rem;
    text-align: center;
    h3{
        color: v.$negro;
        margin: 2rem;
        text-transform: uppercase;
    }
}
.mant-mensual{
    text-align: left;
    margin: 0;
    font-size: 2rem;
}